'use strict';

let baseConfig = {};

let i18n = {
	RQ: '',
	Today: '',
	Yesterday: '',
	GQQT: '7',
	GQSST: '30',

	ALL: '',
	PC: 'PC',
	WAP: '',

	Advertiser: '',
	Agent: '',
	About: '',

	Login: '',
	Logout: ''
}

let apiConfig = {
	login: '/api/login',
	logout: '/api/logout',
	a_summaryall: '/api/agent/summaryall',
	a_costByDay: '/api/agent/costByDay',
	a_clientStat: '/api/agent/clientStat',
	a_dayStat: '/api/agent/dayStat',
	c_summaryall: '/api/client/summaryall',
	c_costByDay: '/api/client/costByDay',
	c_dayStat: '/api/client/dayStat'
};
let mainTable = {
	columns: [{
		field: 'impression',
		title: 'PV',
		formatter: function(value, row, index) {
			value = value === -1 ? '-' : value;
			value = $.trim(value);
			return value;
		}
	}, {
		field: 'click',
		title: '',
		formatter: function(value, row, index) {
			value = value === -1 ? '-' : value;
			value = $.trim(value);
			return value;
		}
	}, {
		field: 'ctr',
		title: 'CTR',
		formatter: function(value, row, index) {
			value = value === -1 ? '-' : value;
			value = $.trim(value);
			return value;
		}
	}, {
		field: 'cpc',
		title: 'CPC',
		formatter: function(value, row, index) {
			value = value === -1 ? '-' : value;
			value = $.trim(value);
			return value;
		}
	}, {
		field: 'eCpm',
		title: 'eCPM',
		formatter: function(value, row, index) {
			value = value === -1 ? '-' : value;
			value = $.trim(value);
			return value;
		}
	}, {
		field: 'cost',
		title: '',
		formatter: function(value, row, index) {
			value = value === -1 ? '-' : value;
			value = $.trim(value);
			return value;
		}
	}],
	responseHandler: function(res) {
		var data = {};
		data.rows = res;
		data.total = res.length;
		return data;
	},
	queryParams: function(params) {
		params.uid = 1;
		return params;
	}
};
let detailTable = {
	showToggle: true,
	search: true,
	pagination: true,
	// sidePagination: 'server',
	// url: apiConfig.a_dayStat,
	pageList: [10, 25, 50, 100, 'ALL'],
	columns: [{
		field: 'statDate',
		sortable: true,
		title: ''
	}, {
		field: 'click',
		title: ''
	}, {
		field: 'cpc',
		title: 'CPC'
	}, {
		field: 'ctr',
		title: 'CTR'
	}],
	responseHandler: function(res) {
		var data = {};
		data.rows = res;
		data.total = res.length;
		return data;
	},
	queryParams: function(params) {
		params.uid = 1;
		return params;
	}
};
let advertiserTable = {
	showToggle: true,
	search: true,
	pagination: true,
	pageList: [10, 25, 50, 100, 'ALL'],
	columns: [{
		field: 'clientName',
		title: ''
	}, {
		field: 'click',
		sortable: true,
		title: ''
	}, {
		field: 'cpc',
		sortable: true,
		title: 'CPC'
	}, {
		field: 'ctr',
		sortable: true,
		title: 'CTR'
	}],
	responseHandler: function(res) {
		var data = {};
		data.rows = res;
		data.total = res.length;
		return data;
	},
	queryParams: function(params) {
		params.uid = 1;
		return params;
	}
};

let config = Object.assign({}, baseConfig, {
	i18n: i18n,
	api: apiConfig,
	tableOption: {
		mainTable: mainTable,
		detailTable: detailTable,
		advertiserTable: advertiserTable
	}
});

module.exports = config;


